import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'Apeshibanati',
  description:
    'The most popular AMM on BSC by user count! Earn CAKE through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by Apeshibanati), NFTs, and more, on a platform you can trust.',
  image: '',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('Apeshibanati')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('Apeshibanati')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('Apeshibanati')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('Apeshibanati')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('Apeshibanati')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('Apeshibanati')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('Apeshibanati')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('Apeshibanati')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('Apeshibanati')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('Apeshibanati')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('Apeshibanati')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('Apeshibanati')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('Apeshibanati')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('Apeshibanati')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('Apeshibanati')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('Apeshibanati')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('Apeshibanati')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('Apeshibanati')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('Apeshibanati')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('Apeshibanati')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('Apeshibanati')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('Apeshibanati Info & Analytics')}`,
        description: 'View statistics for Apeshibanati exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('Apeshibanati Info & Analytics')}`,
        description: 'View statistics for Apeshibanati exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Pools')} | ${t('Apeshibanati Info & Analytics')}`,
        description: 'View statistics for Apeshibanati exchanges.',
      }
    default:
      return null
  }
}
